<template>
	<view class="box-compents">
		<swiper class="swiper" :indicator-dots="indicatorDots" indicator-color="#E6EBF6" indicator-active-color="#0B4A8D" @change="change">
			 <!-- autoplay="true" interval=1000 -->
			<swiper-item v-for="(v,i) in imglist" :key="i" :current="i">
				<view class="swiper-item">
					<view class="swiper-item-img">
						<image :src="v" mode="widthFix"></image>
					</view>
				</view> 
				<view class="experience" v-if="i==imglist.length-1" @tap="launchFlag">立即体验</view>
			</swiper-item>
			
		</swiper>
		<view class="jump-over" @tap="launchFlag">{{seconds}}s 跳过</view>
		<text class="zi">麦子易商提供专业服务</text>
	</view>
</template>

<script>
	var setI
	import {Guide} from "@/network/api.js"
	export default {
		data() {
			return {
				imglist:[],
				seconds:"5",
				indicatorDots:true,
				httpUrl: getApp().globalData.httpUrl,
				// duration: 500
			}
		},
		mounted() {
			this.getImg()
			setI=setInterval(()=>{
				if(this.seconds==1){
					this.launchFlag()
				}else{
					this.seconds-=1
				}
			},1000)
		},
		methods: {
			launchFlag() {
				clearInterval(setI)
				uni.setStorage({
					key: 'launchFlag',
					data: true,
				});
				uni.getStorage({
					key: 'sn',
					success: (res)=> {
						uni.redirectTo({
							url: '/pages/login/login?ifCheckAll=true'
						});
					},
					fail: (err)=> {
						uni.redirectTo({
							url: '/pages/login/register'
						});
					},
				});
			},
			getImg(){
				this.imglist=[]
				Guide().then((res) => {
					console.log("Guide res", res)
					if (res.error_code == 0) {
						for(var i in res.data){
							this.imglist.push(this.httpUrl+res.data[i].urls)
						}
						if(res.data.length<=1){
							this.indicatorDots=false
						}
					}
				}).catch((err) => {
					console.log(err)
				})
			},
			change(e){
				if(e.detail.current==this.imglist.length-1){
					this.indicatorDots=false
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box-compents {
		width: 100%;
		height: 100vh;
		background-size: 100% auto;
		padding: 0;
		margin: 0;
		
		.swiper {
			width: 100%;
			height: 100vh;
			background: #FFFFFF;
		}
		
		/deep/uni-swiper .uni-swiper-dots-horizontal{
			bottom: 80px;
		}
		
		.swiper-item {
			width: 100%;
			height: 100vh;
			text-align: center;
			position: relative;
			display: flex;
			justify-content: center;
			align-items: flex-end;
			flex-direction: column-reverse;
		}
		
		.swiper-item-img {
			width: 100%;
			height: 100vh;
			display: flex;
		}
		uni-image{
			width: 100%;
			margin: auto;
		}
		
		.jump-over,
		.experience {
			position: absolute;
			height: 60upx;
			line-height: 60upx;
			padding: 0 40upx;
			font-size: 32upx;
			color: #454343;
			z-index: 999;
		}
		
		.jump-over {
			right: 45upx;
			top: 125upx;
		}
		.experience {
			right: 50%;
			margin-right: -105upx;
			bottom: 70px;
			padding: 1px 25px;
			color: #fff;
			border-radius: 20px;
			background-image: linear-gradient(to bottom right, #41B0F5, #3B7AAD);
		}
		.zi{
			width: 100%;
			text-align: center;
			font-size: 14px;
			color: #909399;
			position: fixed;
			bottom: 20px;
			left: 0;
		}
	}
</style>